<template>
  <div>
    <Header></Header>
    <div class="article-container">
      <h1 class="title">#{{title}}</h1>
      <div class="tag-container">
        <span class="arctime">
          <i class="el-icon-date">  {{time}}</i>
        </span>
        <span class="tag">
          <i class="el-icon-collection-tag">   {{type}}</i>
        </span>
      </div>
      <div class="md-container">
        <mavon-editor class="md" :value="mdData"
                      :subfield="false"
                      :defaultOpen="'preview'"
                      :toolbarsFlag="false"
                      :editable="false"/>
      </div>
      <el-card class="button">
        <el-button icon="el-icon-star-off" :type='starType' @click="Like" circle></el-button>
        <el-button icon="el-icon-s-comment" @click="comment" circle type="primary"></el-button>
      </el-card>
      <el-card class="socialCard">
        <div class="commentDiv">
          <Comment v-for="item in newsList" :key="item" :data="item"></Comment>
        </div>
        <el-button @click="more" >{{more}}</el-button>
      </el-card>
    </div>
    <el-dialog class="Comment" title="留言" :visible.sync="commentVisable" center :append-to-body='true' :lock-scroll="false" width="60%" style="text-align: center;min-height: 40%">
      <el-form :model="commentForm">
        <el-form-item label="留言内容：" prop="comment">
          <el-input v-model="commentForm.comment" type="textarea" style="width: 100%" :rows="10" placeholder="请输入留言内容"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
      <el-button type="primary" round @click="submitComment(commentForm)">确认留言</el-button>
    </div>
    </el-dialog>
  </div>
</template>

<script>
import Header from '../components/Header.vue'
import Comment from '../components/Comment'
import axios from "axios";
import md from '../assets/README.md'
  export default{
    name:"Article",
    components:{
      Comment,
      Header
    },

    data(){
      return {
        time:"Unknown",
        type:"Unknown",
        mdData: "# 没有获取到内容，可能远端服务器出现了问题！",
        starType:"0",
        title:"未知",
        commentVisable: false,
        commentForm:{
          comment:''
        },
        id: this.$route.query.id,
        commentList:[
          {
            time:"2021年2月28日",
            name:"博主aaaaaa",
            Comment:"d撒打算大撒上的撒的撒打算对d撒打算大撒上的撒的撒打算对d撒打算大撒上的撒的撒打算对d撒打算大撒上的撒的撒打算对d撒打算大撒上的撒的撒打算对d撒打算大撒上的撒的撒打算对撒打算大撒上的撒的撒打算对撒d撒打算大撒上的撒的撒打算对d撒打算大撒上的撒的撒打算对d撒打算大撒上的撒的撒打算对d撒打算大撒上的撒的撒打算对d撒打算大撒上的撒的撒打算对d撒打算大撒上的撒的撒打算对撒打算大撒上的撒的撒打算对撒"
          },
          {
            time:"2021年4月8日",
            name:"23771513@163.com",
            Comment: "oh ,额。。。。。"
          }
          ],
        nomore: 0,
        more:'更多',
        scroll: true, //设置一个开关避免重复请求数据
        pagenum: 1, //当前页数
        totalnum: 3, //总页数
        newsList: [], //页面数据数组
      }
    },
    mounted(){
      axios({
        url :'/api/getArticleData',
        method:'POST',
        data:{
          "id":Number(this.id)
        }
      }).then(response => {
        this.type = response.data.type;
        this.mdData = response.data.md;
        this.time = response.data.time;
        this.title = response.data.name;
      })
      if (window.sessionStorage.getItem("email")!=null){
        this.getLikeStatus();
      }
      axios({
        method:'POST',
        url:"/api/getTotalComment",
        data:{
          "id": this.id
        }
      }).then(res =>{
        this.totalnum=res.data.pages;
      })
    },
    created() {
      this.getComments();
      // window.addEventListener("scroll",this.handleScroll);
    },
    methods:{
      getLikeStatus(){
        axios({
          method:'post',
          url:'/api/getLikeStatus',
          data:{
            "id":Number(this.id)
          }
        }).then(res =>{
          if(res.data.like_tag==1){
            this.starType='danger'
          }
        })
      },
      more(){
        if(this.pagenum+1 > this.totalnum){
          this.more ='没有了';
        }else {
          this.pagenum++;
          this.getComments();
        }
      },
      getComments(){
        axios({
          url:"/api/getCommentList",
          method:"post",
          data:{
            "arc_id":this.id,
            "page":this.pagenum
          }
        }).then(res =>{
          this.newsList =this.newsList.concat(Array(res.data.dataList)[0]);
        })
      },
      // handleScroll () { //滚动事件监听处理
      //   //是否滚动到底部的判断
      //   let scrolltop =
      //     document.documentElement.scrollTop ||
      //     document.body.scrollTop ||
      //     window.pageYOffset;
      //
      //   if (
      //     (scrolltop + document.documentElement.clientHeight ==
      //       document.documentElement.scrollHeight) &
      //     this.scroll
      //   ) {
      //     if (this.pagenum > this.totalnum) {
      //       //如果大于总页数停止请求数据
      //       this.nomore = 1;
      //       console.log("没有更多了");
      //     } else {
      //       this.getSpecialData();
      //       this.scroll = false;
      //     }
      //   }
      // },
      // getSpecialData() {
      //   const params = {
      //     // area_id: this.area_id,
      //     page: this.pagenum,
      //     arc:this.id
      //   };
      //
      //   function getNewsList(params) {
      //     axios({
      //         method:'post',
      //         url: '/user/getCommentList',
      //         data:{
      //           "arc_id":params.arc,
      //           "page":params.page
      //         }
      //       }
      //     )
      //   }
      //   getNewsList(params).then(res => {
      //       this.newsList = [...this.newsList, ...Array(res.data.list)[0]];
      //       this.scroll = true;
      //     ++this.pagenum;
      //     // console.log(this.pagenum);
      //   });
      // },
      comment(){
        if (sessionStorage.getItem("email")==null){
          this.$message.warning("亲，咱们先登陆再留言好吗！")
        }else {
          this.commentVisable=true
        }
      },
      submitComment(commentForm){
        axios({
          method:'POST',
          url:'/api/comment',
          data:{
            "content":commentForm.comment,
            "arc_id":this.id
          }
        }).then(res =>{
          if (res.data.code==200){
            this.commentVisable=false;
            this.$message("留言成功");
          }else {
            this.$message("留言失败！")
          }
        })
      },
      Like(){
        if(sessionStorage.getItem('email')!=null){
        if(this.starType=='danger'){
          axios({
            method:'POST',
            url:'/api/ClickLike',
            data:{
              "arc_id":this.id
            }
          }).then(res =>{
            if(res.data.code==200){
              this.starType='0';
              this.$message("取消点赞成功")
            }
          })
        }else{
          axios({
            method:'POST',
            url:'/api/ClickLike',
            data:{
              "arc_id":this.id
            }
          }).then(res =>{
            if(res.data.code==200){
              this.starType='danger';
              this.$message("点赞成功")
            }
          })
        }
      } else{
        this.$message('亲，没有登陆无法点赞哦！')
      }
    }
  }
  }


</script>

<style scoped>
  .article-container{
    border-radius: 15px;
    position: absolute;
    min-height: 500px;
    background-color: rgba(255,255,255,0.7);
    z-index: 1;
    right: 5%;
    left: 5%;
    top:120px;
    overflow:hidden;
    margin-bottom: 50px;
  }
  .title{
    position: relative;
    color: rgb(64, 62, 67);
    left: 70px;
    width: 80%;
  }
  .tag-container{
    height: 20px;
    right: 60px;
    text-align: right;
    position: relative;
  }
  .arctime{
    position: relative;
    background-color: rgb(255, 78, 106);
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    box-sizing: border-box;
    color: rgb(255, 255, 255);
    cursor: pointer;
    display: inline-block;
    font-family: Roboto, Noto, Helvetica, Arial, sans-serif;
    font-size: 14px;
    height: 26px;
    line-height: 16.100000381469727px;
    margin-bottom: 5px;
    margin-right: 3px;
    padding-bottom: 5px;
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 5px;
    width: 199.890625px;
    text-align: left;
  }
  .tag{
    position: relative;
    background-color: rgb(200, 160, 106);
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    box-sizing: border-box;
    color: rgb(255, 255, 255);
    cursor: pointer;
    display: inline-block;
    font-family: Roboto, Noto, Helvetica, Arial, sans-serif;
    font-size: 14px;
    height: 26px;
    line-height: 16.100000381469727px;
    margin-bottom: 5px;
    margin-right: 3px;
    padding-bottom: 5px;
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 5px;
    width: 129px;
    text-align: left;
  }
  .md-container{
    position: relative;
    top: 20px;
    width: auto;
    border-radius: 15px;
    width: 90%;
    left: 5%;
    background-color: #FFFFFF;
  }
  .socialCard{
    position: relative;
    width: 90%;
    margin: auto;
    border-radius: 15px;
    text-align: center;
    min-height: 200px;
    background-color: rgba(255, 255,255, 0.5);
    border-color: rgba(0,0,0,0);
    box-shadow: 0 2px 12px 0 rgba(246, 167, 176, 0.1);
    clear: both;
    margin-bottom: 40px;
  }
  .md{
    width: 96%;
    left: 2%;
  }
  .button{
    position: relative;
    margin-top: 30px;
    margin-bottom: 10px;
    width: 20%;
    left:74%;
    border-radius: 15px;
    text-align: center;
    border-radius: 30px;
    background-color: rgba(255,255,255,0.4);
  }

</style>



