<template xmlns="http://www.w3.org/1999/html">
<!-- 首页头部菜单栏 -->
  <div class="Headbox">
      <el-menu router :default-active="activeIndex" class="el-menu-header" mode="horizontal" @select="handleSelect" background-color=rgba(240,108,123) text-color="#fff" active-text-color="#409EFF">
      <el-menu-item index="/index" @click="">首页</el-menu-item>
       <el-submenu>
          <template slot="title">分类</template>
            <el-menu-item v-for="item in typeList" :key="item" :index="'/type/'+item.type">{{item.type}}</el-menu-item>
          </el-submenu>
       <el-menu-item index="3" @click="aboutVisable = true">关于</el-menu-item>
       <div class="searchbox">
       <el-input placeholder="请输入文章名或关键字" v-model="input_search">
         <el-button slot="append" icon="el-icon-search" @click="goToSearch(input_search)"></el-button>
       </el-input>
       </div>
       <div class="loginbox">
         <el-button class="login-button" type="info" @click="login">{{loginBtnStatus}}</el-button>
       </div>
       </el-menu>
      <el-dialog class="loginDia" title="登录" :visible.sync="dialogFormVisible"  center :append-to-body='true' :lock-scroll="false" width="40%" >
        <el-form :model="loginForm">
          <el-form-item label="邮箱" prop="email" label-width="90px">
            <el-input class="el-input1" v-model="loginForm.email"></el-input>
            <el-button @click="getCode" :class="{'disabled-style':getCodeBtnDisable}" :disabled="getCodeBtnDisable">{{codeBtnWord}}</el-button>
          </el-form-item>

          <el-form-item label="邮箱验证码" prop="passcode" label-width="90px">
            <el-input class="el-input1" v-model="loginForm.passcode"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false; resetForm('loginForm')" round>取 消</el-button>
          <el-button type="primary" @click="submitForm" round>登 录</el-button>
        </div>
      </el-dialog>
    <el-dialog class="about" title="关于" :visible.sync="aboutVisable" center :append-to-body='true' :lock-scroll="false" width="60%" style="text-align: center;min-height: 40%">
      <p>这就是一个简单的博客！</p>
      <p>当前登陆用户:{{user}}</p>
    </el-dialog>
  </div>
</template>

<script>
import axios from "axios";
import {mapMutations} from "vuex";

export default {
  name:'Header',
  data() {
      return {
        user:'未登陆',
        input_search: '',
        dialogFormVisible: false,
        activeIndex: '1',
        typeList:[],
        loginForm: {
          email: '',
          passcode: ''
        },
        waitTime:60,
        codeBtnWord:"获取",
        getCodeBtnDisable: false,
        loginStatus:'0',
        aboutVisable:false,
        loginBtnStatus: '登陆'
      };

    },
    methods: {
    login(){
      if(sessionStorage.getItem('email')==null){
        this.dialogFormVisible=true
      }else {
        this.loginBtnStatus = '登陆'
        sessionStorage.clear()
      }
    },
      handleSelect(key, keyPath) {
        console.log(key, keyPath);
      },
      getList:function (){
        axios({url:'/api/getArcTypeList',
          method:'post'
        }).then(response =>{
          this.typeList=Array(response.data.dataList)[0];
          console.log(response.data.dataList);
        })
      },
      Timer() {
        const that = this
        that.waitTime--
        that.getCodeBtnDisable = true
        this.codeBtnWord = `${this.waitTime}s 后重新获取`
        console.log('获取时间的值2：' + this.waitTime)
        const timer = setInterval(function () {
          if (that.waitTime > 1) {
            that.waitTime--
            that.codeBtnWord = `${that.waitTime}s 后重新获取`
          } else {
            clearInterval(timer)
            that.codeBtnWord = '获取验证码'
            that.getCodeBtnDisable = false
            that.waitTime = 61
          }
        }, 1000)
      },
      getCode() {
        axios({
          method:'POST',
          url:"/api/getMailCode",
          data:{
            "email":this.loginForm.email
          }
        }).then(res => {
          console.log(res.data);
          if (res.data.code == 200) {
            this.Timer()
          }
        })
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      submitForm(){
        axios({
          method:'POST',
          url:'/api/userLoginCheck',
          data: {
            "email":this.loginForm.email,
            "code":this.loginForm.passcode
          }
        }).then(res =>{
          this.loginStatus=res.data.code;
          console.log(res.data.code);
        })
      },
      goToSearch(input_search){
      this.$router.push("/search/"+input_search);
      },
    },
    mounted() {
    if(sessionStorage.getItem("email") == null){
    }else {
      this.dialogFormVisible=false;
      this.user=sessionStorage.getItem('email');
      this.loginBtnStatus='退出';
    };
    console.log(sessionStorage.getItem("email"));
    this.getList();
  },
  watch:{
    'loginStatus':function (newVal){
      if(newVal == '2333'){
        this.$message('邮箱输入有误');
      }else if (newVal == '255'){
        this.$message('邮件发送错误');
      }else if(newVal == '144'){
        this.$message('验证码错误');
      }else if(newVal == '10010'){
        this.$message('欢迎登陆，我的新朋友');
        sessionStorage.setItem('email',this.loginForm.email);
        this.user=sessionStorage.getItem('email');
        this.dialogFormVisible=false;
        this.loginBtnStatus='退出';
      }else if(newVal == '555'){
        this.$message('欢迎回来，我的老伙计');
        this.dialogFormVisible=false;
        sessionStorage.setItem('email',this.loginForm.email);
        this.user=sessionStorage.getItem('email');
        this.loginBtnStatus='退出';
      }
    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.searchbox{
  position: absolute;
  right: 80px;
  width: 300px;
  padding: 9px;
}
.loginbox{
  position: absolute;
  right: 0;
  padding: 9px;
}
.login-button{
  background-color: rgba(255,255,255,0.4);
  border: white;
}
.el-input1{
  width:80% ;
}
</style>



