<template>
  <div>
    <el-card class="comment">
      <div class="img-container">
        <i class="el-icon-user"></i>
      </div>
      <div class="text">{{data.Comment}}</div>
      <div class="blogfill">{{data.name}} : {{data.time}}</div>
    </el-card>
  </div>
</template>
<script>
export default {
  props:["data"],
  name:"Comment",
  data(){
    return{

    }
  }
}
</script>
<style scoped>
.comment{
  height: 180px;
  border-radius: 90px;
  margin-bottom:20px ;
}
.img-container{
  height: 60px;
  width: 60px;
  top: 38px;
  border-radius: 30px;
  background-color: rgb(63,158,255);
  position: relative;
  margin-left: 2%;
  font-size: 28px;
}
.el-icon-user{
  top:19%;
  color: #FFFFFF;
  position: relative;
}
.text{
  text-align: left;
  position: relative;
  height: 70px;
  width: 80%;
  left: 120px;
  right: 5%;
  top: -50px;
  -webkit-text-fill-color: #908e91;
}
.blogfill{
  position: relative;
  padding-right: 10.5%;
  text-align: right;
  -webkit-text-fill-color: rgb(160,178,193);
}
</style>



