import Vue from 'vue'
import Router from 'vue-router'
import Article from '../pages/Article.vue'
import Home from '../pages/Home.vue'
import type from "../pages/type";
import search from "../pages/search";

Vue.use(Router)

export default new Router({
  mode:"history",
    routes:[
        {
            path:'/',
            redirect: '/index'
        },

        {
            path:  '/index',
            name: 'Home',
            component:  Home,
            meta: {
                auth: false
            }
        },
        {
          path: '/article',
          name: 'article',
          component: Article,
          meta:{
            auth:false
          },
        },
      {
        path: '/type/:type',
        name: 'type',
        component: type,
        meta:{
          auth: false
        }
      },
      {
        path: '/search/:word',
        name: 'search',
        component:search,
        meta:{
          auth: false
        }
      }
    ]
})



